import React,{ useState } from 'react'
import styled from 'styled-components'
import { Link, useLocation } from 'react-router-dom'
import Settings from './Settings'

/* */
import menuIcon from "../images/icons/menu.png"
import profileIcon from "../images/icons/profile.png"
import analyticsIcon from "../images/icons/analytics.png"
import youtubeIcon from "../images/icons/youtube logo.png"
import recentIcon from "../images/icons/recent.png"
import settingsIcon from "../images/icons/settings.png"

var opened = false;

function Navbar(props){
    const [selectedPosition, setSelectedPosition] = useState("6rem")

    function navBarClick(){
        if(opened == false){
            document.getElementById("navbar").style.width = "20rem"
            document.getElementById("box").style.width = "20rem"
            opened = true;
        }
        else{
            document.getElementById("navbar").style.width = "3.5rem"
            document.getElementById("box").style.width = "3.5rem"
            opened = false;
        }
    }
    
    function closeNav(iconId){
        var element = document.getElementById(iconId)
        var px = (100 / [document.documentElement.clientWidth] )
        setSelectedPosition((element.offsetTop)*px+"vw")
        document.getElementById("box").style.width = "3.5rem"
        window.scrollTo(0, 0)
        document.getElementById("navbar").style.width = "3.5rem"
        opened = false
    }

    var path = useLocation().pathname

    const Nav = styled.div` 
        display: ${path !== "/" ? 'block' : 'none'};
    `
    const Box = styled.div`
        pointer-events: none;
        width: 3.5rem;
        height: 4rem;
        background-color: #ff3d3d10;
        top: ${selectedPosition};
        position: fixed;
        z-index: 3;
        overflow: hidden;
        transition: all ease-in-out 0.3s;
    `
    return(
        <Nav className="navbar" id="navbar">
            <img id = "menuicon" className="menu-icon" src={menuIcon} onClick={navBarClick}></img>
            <Box id="box">
                <div className="rect" />
            </Box>
            <Link to="/Profile"  style={{ textDecoration: 'none' }}>
            <div id="profileicon" className = "navlink" onClick={()=> closeNav("profileicon")}>
                <img className="navbaricon"  src={profileIcon} />
                <p className="navlinktext"> Profile</p>
            </div>
            </Link>

            <Link to="/ChannelAnalytics" style={{ textDecoration: 'none' }}>
            <div id="analyticsicon" className = "navlink" onClick={()=> closeNav("analyticsicon")}>
                <img className="navbaricon" src={analyticsIcon} />
                <p  className="navlinktext">Channel Analytics</p>
            </div>
            </Link>

            <Link to="/WatchStatistics" style={{ textDecoration: 'none' }}>
            <div id="statsicon" className = "navlink" onClick={()=> closeNav("statsicon")}>
                <img className="navbaricon" src={youtubeIcon} />
                <p className="navlinktext">Watch Statistics</p>
            </div>
            </Link>

            <Link to="/RecentActivity" style={{ textDecoration: 'none' }}>
            <div id="activityicon" className = "navlink" onClick={()=> closeNav("activityicon")}>
                <img className="navbaricon" src={recentIcon} />
                <p className="navlinktext">Recent Activity</p>
            </div>
            </Link>
            <Settings />

        </Nav>
    )
}

export default Navbar