import React from 'react'
import styled from 'styled-components'
import { Link, useLocation } from 'react-router-dom'
import Button from '@material-ui/core/Button'

/* */
import youtubeLogo from "../images/icons/brightlogo.svg"

export default function TopBar(props){
    var path = useLocation().pathname

    const Bar = styled.div`
        display: ${path !== "/" ? 'flex' : 'none'};
        justify-content: left;
        align-items: center;
        position: fixed;
        right: 0;
        top: 0;
        width: calc(100%);
        height: 2.5rem;
        padding: 0.5rem;
        z-index: 3;
        background-color: white;
        border: 1px solid rgba(0, 0, 0, 0.1);
        box-shadow: 0px 0px 8px 2px #00000020;
    `
    const Title = styled.h1`
        font-size: 1.5rem;
        width: 82.5%;
        letter-spacing: -1px;
        opacity: 0.8;
    `
    return(
        <Bar>
            <img src={youtubeLogo} style={{width: "2rem", marginRight: "0.75rem", marginLeft: "5rem"}}></img>
            <Title>Profile</Title>
            <Link to="/"  style={{ textDecoration: 'none' }}>
            <Button style={{fontSize: "0.75rem", width: "5.5rem", height: "1.5rem", margin: "0.5rem", marginRight: "auto", color: "white", backgroundColor: "#ff3d3d"}} variant="contained">Log Out</Button>
            </Link>
            <Button style={{fontSize: "0.75rem", width: "10rem", height: "1.5rem",  margin: "0.5rem", marginRight: "0", color: "white", backgroundColor: "#ff3d3d"}} variant="contained" >Switch Accounts</Button>
        </Bar>
    )
}