import React, { useState, useEffect } from 'react'
import ReactTooltip from 'react-tooltip'
import FadeIn from 'react-fade-in'

/* */
import youtubeIcon from "../images/icons/youtube logo.png"
import linkIcon from '../images/icons/link.png'

import config from './Youtube/config'

function ProfileInfo(props){
    const [name, setName] = useState(localStorage.getItem("userName"))
    const [photo, setPhoto] = useState(localStorage.getItem("profilePhoto"))
    const [subscriberCount, setSubscriberCount] = useState(localStorage.getItem("subscriberCount"))
    const [viewCount, setViewCount] = useState(localStorage.getItem("viewCount"))
    const [vidCount, setVidCount] = useState(localStorage.getItem("videoCount"))
    const { api_key, channel_id} = config
    console.log("CHANEELEL: ID" + JSON.stringify(config.channel_id))



    var channelUrl=`https://youtube.com/channel/${props.channelLink}`
     return(
        <div className="profile-info-container">
            <img className="profile-pic" src={photo} />
            <div className="profile-info">
                <FadeIn delay="150">
                    <h1 className="user-name">
                        {name}
                        <a data-tip="View Channel on Youtube" href={channelUrl} target="_blank">
                            <img className="youtube-link-icon" src={linkIcon} />
                            <ReactTooltip />
                        </a>    
                    </h1>
                    <ul style={{listStyleType: "none", margin: "none"}}>
                        <li className="subscriber-count">{subscriberCount} Subscribers</li>
                        <li className="view-count">{viewCount} Views</li>
                        <li className="video-count">{vidCount} Videos</li>
                    </ul>
                </FadeIn>
            </div>
        </div>
     )
 }
export default ProfileInfo